<template>
  <div class="logup">
    <the-navbar />
    <main class="main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12 col-lg-9">
            <div class="row">
              <div class="col-12 col-sm-8 offset-sm-2">
                <div class="card shadow-sm border-0 mb-4">
                  <div class="card-body p-3">
                    <form @submit.prevent="createUser">
                      <h1 class="h3 text-center mb-4 text-uppercase">
                        Registro
                      </h1>
                      <div class="form-group">
                        <label for="name">Nombre completo</label>
                        <input
                          type="text"
                          class="form-control"
                          v-model="form.displayName"
                          id="name"
                          placeholder="Juan Pérez"
                        />
                      </div>
                      <div class="form-group">
                        <label for="email">Correo electrónico</label>
                        <input
                          type="email"
                          class="form-control"
                          v-model="form.email"
                          id="email"
                          placeholder="juanperez@email.com"
                        />
                      </div>
                      <div class="form-group">
                        <label for="password">Password</label>
                        <input
                          type="password"
                          class="form-control"
                          v-model="form.password"
                          id="password"
                          placeholder="********"
                        />
                      </div>
                      <div class="form-group">
                        <label for="password">Confirmar password</label>
                        <input
                          type="password"
                          class="form-control"
                          v-model="form.passwordConfirm"
                          id="password_confirm"
                          placeholder="********"
                        />
                      </div>
                      <button type="submit" class="btn btn-primary btn-block">
                        Registrarme
                      </button>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-12 col-lg-3">
            <div class="row">
              <div class="col-12 col-sm-4 col-lg-12  offset-sm-4 offset-lg-12">
                <Adds />
              </div>
              <div class="col-12">
                <the-footer />
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { fb } from "@/firebase";
import { db } from "@/main";

import Adds from "@/components/Adds";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

import axios from "axios";

export default {
  name: "Logup",
  components: {
    Adds,
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      form: {
        displayName: "",
        email: "",
        password: "",
        passwordConfirm: ""
      },
      users: []
    };
  },
  created() {
    this.getUsers();
  },
  methods: {
    async createUser() {
      if (String(this.form.password) === String(this.form.passwordConfirm)) {
        fb.auth()
          .createUserWithEmailAndPassword(this.form.email, this.form.password)
          .then(async user => {
            await this.addUser(user.user);

            this.reset();
            this.$router.replace("/muro");
          })
          .catch(error => {
            var errorCode = error.code;
            var errorMessage = error.message;
            if (errorCode == "auth/weak-password") {
              alert("The password is too weak");
            } else {
              alert(errorMessage);
            }
            console.log(error);
          });
      } else {
        console.error({ error: `Password don´t match` });
      }
    },
    async addUser(data) {
      const body = {
        displayName: data.displayName || this.name,
        email: data.email,
        phoneNumber: data.phoneNumber,
        photoURL: data.photoURL,
        providerId: data.providerId,
        uid: data.uid
      };
      await axios
        .post(`${db}/users`, body, {
          headers: {
            "Content-Type": "application/json"
          }
        })
        .then(result => console.log(result))
        .catch(error => {
          var errorCode = error.code;
          var errorMessage = error.message;
          if (errorCode == "auth/operation-not-allowed") {
            alert("Operation not allowed");
          } else {
            alert(errorMessage);
          }
          console.log(error);
        });
    },
    async getUsers() {},
    reset() {
      Object.assign(this.$data, this.$options.data.apply(this));
    }
  }
};
</script>

<style scoped></style>
